function setShadow() {
  const optionsAll = document.querySelectorAll('[data-options]'),
    prev = document.querySelector('.preview');

  const options = {}; // Ð¾Ð±ÑÐµÐºÑ Ñ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑÐ°Ð¼Ð¸ ÑÐµÐ½Ð¸
  const colorRgbaShadow = {};// Ð¾Ð±ÑÐµÐºÑ Ñ ÑÐ²ÐµÑÐ¾Ð¼ ÑÐµÐ½Ð¸
  const colorRgbaPrev = {};// Ð¾Ð±ÑÐµÐºÑ Ñ ÑÐ²ÐµÑÐ¾Ð¼ .prev




  setStartColorRgbaShadow(); // Ð·Ð°Ð¿Ð¾Ð»Ð½Ð¸ÑÑ colorRgbaShadow Ð·Ð½Ð°ÑÐµÐ½Ð¸ÑÐ¼Ð¸ Ð¿Ð¾ ÑÐ¼Ð¾Ð»ÑÐ°Ð½Ð¸Ñ
  setStartOptions(); //Ð·Ð°Ð¿Ð¾Ð»Ð½Ð¸ÑÑ options Ð·Ð½Ð°ÑÐµÐ½Ð¸ÑÐ¼Ð¸ Ð¿Ð¾ ÑÐ¼Ð¾Ð»ÑÐ°Ð½Ð¸Ñ
  setBoxShadow(); // ÑÑÐ½ÐºÑÐ¸Ñ Ð·Ð°Ð´Ð°ÐµÑ ÑÑÐ¸Ð»Ð¸ ÑÐµÐ½Ð¸
  setCustomOptions(); // ÑÑÐ½ÐºÑÐ¸Ñ Ð½Ð°Ð²ÐµÑÐ¸Ð²Ð°ÐµÑ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸ÐºÐ¸ Ð½Ð° input's

  console.log(options);
  console.log(colorRgbaShadow);


  /***Functions***/
  /*============*/

  /*ÐºÐ¾Ð½Ð²ÐµÑÑÐ°ÑÐ¸Ñ HEX Ð² RGB*/
  function convertColor(color) {
    if (color.substring(0, 1) == '#') { //Ð£Ð´Ð°Ð»Ð¸ÑÑ # Ð² Ð·Ð½Ð°ÑÐµÐ½Ð¸Ð¸ hex-ÑÐ²ÐµÑÐ°
      color = color.substring(1);
    }

    colorRgbaShadow.r = parseInt(color.substring(0, 2), 16); // Ð´ÐµÐºÐ¾Ð´Ð¸ÑÐ¾Ð²Ð°Ð½Ð¸Ðµ
    colorRgbaShadow.g = parseInt(color.substring(2, 4), 16); // Ð´ÐµÐºÐ¾Ð´Ð¸ÑÐ¾Ð²Ð°Ð½Ð¸Ðµ
    colorRgbaShadow.b = parseInt(color.substring(4), 16); // Ð´ÐµÐºÐ¾Ð´Ð¸ÑÐ¾Ð²Ð°Ð½Ð¸Ðµ
  }

  /*Ð·Ð°Ð¿Ð¾Ð»Ð½Ð¸ÑÑ colorRgbaShadow Ð·Ð½Ð°ÑÐµÐ½Ð¸ÑÐ¼Ð¸ Ð¿Ð¾ ÑÐ¼Ð¾Ð»ÑÐ°Ð½Ð¸Ñ*/
  function setStartColorRgbaShadow() {
    optionsAll.forEach(item => {
      if (item.getAttribute('data-options') == 'shadow-color') {
        convertColor(item.value);
      }

      if (item.getAttribute('data-options') == 'opacity') {
        colorRgbaShadow.opacity = +item.value;
      }
    });
  }

  /*Ð·Ð°Ð¿Ð¾Ð»Ð½Ð¸ÑÑ options Ð·Ð½Ð°ÑÐµÐ½Ð¸ÑÐ¼Ð¸ Ð¿Ð¾ ÑÐ¼Ð¾Ð»ÑÐ°Ð½Ð¸Ñ*/
  function setStartOptions() {
    optionsAll.forEach(item => {
      if (item.getAttribute('data-options') == 'offsetX') {
        options.offsetX = +item.value;
      }

      if (item.getAttribute('data-options') == 'offsetY') {
        options.offsetY = +item.value;
      }

      if (item.getAttribute('data-options') == 'blur') {
        options.blur = +item.value;
      }

      if (item.getAttribute('data-options') == 'spread') {
        options.spread = +item.value;
      }
    });

  }

  //Ð·Ð°Ð´Ð°Ð½Ð¸Ðµ ÑÑÐ¸Ð»ÐµÐ¹ ÑÐµÐ½Ð¸
  function setBoxShadow() {
    prev.style.boxShadow = `${options.offsetX}px ${options.offsetY}px ${options.blur}px ${options.spread}px rgba(${colorRgbaShadow.r}, ${colorRgbaShadow.b}, ${colorRgbaShadow.b}, ${colorRgbaShadow.opacity})`;
  }

  /*Ð·Ð°Ð´Ð°Ð½Ð¸Ðµ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸ÐºÐ¾Ð²*/
  function setCustomOptions() {
    optionsAll.forEach(item => {
      if (item.getAttribute('data-options') == 'offsetX') {
        item.addEventListener('input', () => {
          options.offsetX = +item.value;
          setBoxShadow();
        });
      }

      if (item.getAttribute('data-options') == 'offsetY') {
        item.addEventListener('input', () => {
          options.offsetY = +item.value;
          setBoxShadow();
        });
      }

      if (item.getAttribute('data-options') == 'blur') {
        item.addEventListener('input', () => {
          options.blur = +item.value;
          setBoxShadow();
        });
      }

      if (item.getAttribute('data-options') == 'spread') {
        console.log(`spread`);
        item.addEventListener('input', () => {
          options.spread = +item.value;
          setBoxShadow();
        });
      }

      if (item.getAttribute('data-options') == 'opacity') {
        console.log(`opacity`);
        item.addEventListener('input', () => {
          colorRgbaShadow.opacity = +item.value;
          setBoxShadow();
        });
      }

      if (item.getAttribute('data-options') == 'shadow-color') {
        console.log(`shadow-color`);
        item.addEventListener('input', () => {
          convertColor(item.value);
          setBoxShadow();
        });
      }
    });
  }
}

export default setShadow;
