function customRange() {

  /*Ð¿ÑÐ¾Ð²ÐµÑÐºÐ° Ð½Ð° IE - (ÑÑÑÐº: Ð¾Ð½ Ð½Ðµ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·ÑÐµÑ append)*/
  const div = document.createElement('div');
  try {
    !!document.body.append(div); // Ð¿ÑÐ¸Ð²Ð¾Ð´Ð¸Ð¼ ÑÐµÐ·ÑÐ»ÑÑÐ°Ñ Ðº Ð±ÑÐ»ÐµÐ²Ð¾Ð¼Ñ ÑÐ¸Ð¿Ñ (true Ð¸Ð»Ð¸ false) Ñ Ð¿Ð¾Ð¼Ð¾ÑÑÑ Ð¿ÑÐ¸ÑÐ¼Ð° Ð´Ð²Ð¾Ð¹Ð½Ð¾Ð³Ð¾ Ð¾ÑÑÐ¸ÑÐ°Ð½Ð¸Ñ
  } catch (error) {
    document.querySelector('.modal').style.display = 'block';
    document.querySelector('.page-main').style.display = 'none';
    return;
  }
  /*****/

  const rangeAll = document.querySelectorAll('[data-range]');

  rangeAll.forEach(item => {
    /*Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ ÑÐ°Ð·Ð¼ÐµÑÐºÑ Ð´Ð»Ñ Ð²Ð¸Ð·ÑÐ°Ð»Ð¸Ð·Ð°ÑÐ¸Ð¸ ÑÑÐµÐºÐ° Ð¸ Ð¿Ð¾Ð»Ð·ÑÐ½ÐºÐ°*/
    item.insertAdjacentHTML('beforeend', '<div class="range__wrapper"><div class="range__wrap-inner"><div class="range__track"></div><div class="range__progress"></div></div><div class="range__handle"></div></div>');
    /*Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ ÑÐ°Ð·Ð¼ÐµÑÐºÑ Ð¾ÑÐ¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ñ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ñ value*/
    item.insertAdjacentHTML('beforeend', '<div class="range__value">');

    /*================*/
    const rangeInput = item.querySelector(".range__input"),
      rangeInputMin = rangeInput.min,
      rangeInputMax = rangeInput.max,
      rangeMinMax = rangeInputMax - rangeInputMin; // ÑÑÐ¾ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ Ð½ÑÐ¶Ð½Ð¾, ÑÑÐ¾ Ð±Ñ ÐºÐ¾ÑÑÐµÐºÑÐ½Ð¾ Ð²ÑÑÐ¸ÑÐ»ÑÑÑ ÑÐ¸ÑÐ¸Ð½Ñ progress Ð¸ Ð¿Ð¾Ð»Ð¾Ð¶ÐµÐ½Ð¸Ðµ handle Ð¿ÑÐ¸ input min Ð¾ÑÐ»Ð¸ÑÐ½Ð¾Ð¼ Ð¾Ñ 0.


    const rangeProgress = item.querySelector(".range__progress"),
      rangeHandle = item.querySelector(".range__handle"),
      output = item.querySelector('.range__value');

    output.innerHTML = rangeInput.value; //Ð²ÑÐ²Ð¾Ð¸Ð¼ Ð½Ð°ÑÐ°Ð»ÑÐ½Ð¾Ðµ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ value

    const widthRange = item.querySelector('.range__wrap-inner').offsetWidth; //ÑÐ·Ð½Ð°ÑÑ ÑÐ¸ÑÐ¸Ð½Ñ range
    const widthHandle = item.querySelector('.range__handle').offsetWidth; // ÑÐ·Ð½Ð°ÑÑ ÑÐ¸ÑÐ¸Ð½Ñ .range__handle

    rangeInput.style.opacity = '0'; // Ð·Ð°Ð´Ð°ÑÑ ÑÑÐ°Ð½Ð´Ð°ÑÑÐ½Ð¾Ð¼Ñ input "opacity: 0;"

    rangeProgress.style.width = ((rangeInput.value - rangeInputMin) / rangeMinMax) * widthRange + 'px'; // ÑÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ Ð½Ð°ÑÐ°Ð»ÑÐ½ÑÑ ÑÐ¸ÑÐ¸Ð½Ñ progress

    rangeHandle.style.left = (widthRange - (((rangeInput.value - rangeInputMin) / rangeMinMax) * widthRange) - (widthHandle / 2)) + 'px'; // ÑÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ Ð½Ð°ÑÐ°Ð»ÑÐ½ÑÑ ÑÐ¾ÑÐºÑ handle

    // ÐÐ·Ð¼ÐµÐ½Ð¸ÑÑ ÑÐ¸ÑÐ¸Ð½Ñ .range__progress Ð¸ ÐºÐ¾Ð¾ÑÐ´Ð¸Ð½Ð°ÑÑ handle (ÐºÐ°Ð¶Ð´ÑÐ¹ ÑÐ°Ð· ÐºÐ¾Ð³Ð´Ð° Ð´Ð²Ð¸Ð³Ð°ÐµÐ¼ Ð¿Ð¾Ð»Ð·ÑÐ½Ð¾Ðº)  oninput/onchange
    rangeInput.oninput = function () {
      output.innerHTML = this.value;
      rangeProgress.style.width = (1 - ((this.value - rangeInputMin) / rangeMinMax)) * widthRange + 'px'; //ÑÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ ÑÐ¸ÑÐ¸Ð½Ñ rangeProgress
      rangeHandle.style.left = (((this.value - rangeInputMin) / rangeMinMax) * widthRange - (widthHandle / 2)) + 'px'; //ÑÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ left Ð´Ð»Ñ  rangeHandle
    };
  });
}

export default customRange;
